import { useRef, useEffect } from "react";
import AppNavbar from "./AppNavbar";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-icons/font/bootstrap-icons.css";

import logoHome from "./images/logoHome.png";
import logoSmall from "./images/logoSmall.png";
import peoples from "./images/peoples.png";
import calendar from "./images/calendar.png";


const App = () => {
  const navbarRef = useRef();
  const featuresRef = useRef();
  const serviceIntroRef = useRef();

  const FeatureItem = ({ imgSrc, imgAlt, title, description, benefits }) => (
    <div className="feature-item">
      <div className="feature-image-container">
        <img src={imgSrc} alt={imgAlt} className="feature-image" />
      </div>
      <div className="feature-text-container">
        <h3 className="feature-title">{title}</h3>
        <p className="feature-description">{description}</p>
        
        {benefits && (
          <div className="feature-benefits">
            {benefits.map((benefit, index) => (
              <div key={index} className="feature-benefit-item">
                <div className="benefit-icon"></div>
                <span>{benefit}</span>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );

  useEffect(() => {
    const handleScroll = () => {
      // Features   
      if (featuresRef.current) {
        const featureItems = featuresRef.current.querySelectorAll(".feature-item");
        
        featureItems.forEach((item) => {
          const rect = item.getBoundingClientRect();
          const windowHeight = window.innerHeight;
          
          if (rect.top < windowHeight * 0.8) {
            item.classList.add("animate-in");
          }
        });
      }

      //     
      if (serviceIntroRef.current) {
        const rect = serviceIntroRef.current.getBoundingClientRect();
        const windowHeight = window.innerHeight;
        
        if (rect.top < windowHeight * 0.7) {
          serviceIntroRef.current.classList.add("animate-in");
        }
      }
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll();

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <div className="main-layout home-page">
      <div className="transparent-navbar-wrapper" style={{ position: 'absolute', zIndex: 1050 }}>
        <AppNavbar 
          transparent={true}
          ref={navbarRef}
        />
      </div>
      
      <main className="main-content">
        <section className="hero-section">
          <div className="hero-content">
            <div className="hero-text-section">
              <h1 className="hero-title">
                Welcome<br />
                <span className="hero-title-highlight">to</span><br />
                StudyBuddy
              </h1>
              <p className="hero-description">
                     , StudyBuddy<br />
                   .
              </p>
              
              <div className="hero-buttons">
                <button 
                  type="button" 
                  className="hero-button-primary"
                >
                  
                </button>
                <button 
                  type="button" 
                  className="hero-button-secondary"
                >
                  
                </button>
              </div>
            </div>
          </div>
          
          <div className="hero-image-container">
            <img 
              src={logoHome}
              alt="StudyBuddy  " 
              className="hero-main-image"
              loading="eager"
            />
          </div>
        </section>

        <section className="service-intro-section" ref={serviceIntroRef}>
          <div className="service-intro-content">
            <div className="service-image-container">
              <img 
                src={logoSmall}
                alt="StudyBuddy  " 
                className="service-intro-image"
              />
            </div>
            <div className="service-text-container">
              <p className="service-subtitle">How we help</p>
              <h2 className="service-title">
                   ,<br />
                  !
              </h2>
              <p className="service-text">Jack Sparrow (Current User)</p>
            </div>
            <div className="service-testimonials">
              <div className="testimonial-avatars">
                <div className="testimonial-avatar">
                  <img src={logoHome} alt="  1" className="avatar-image" />
                </div>
              </div>
              <div className="testimonial-content">
                <span className="testimonial-rating">: </span>
              </div>
            </div>
          </div>
        </section>

        <section className="features-section" ref={featuresRef}>
          <FeatureItem 
            imgSrc={peoples}
            imgAlt=" " 
            title=" " 
            description="       .        ."
            benefits={[
              "  ",
              "   ",
              "  "
            ]}
          />
          
          <FeatureItem 
            imgSrc={calendar}
            imgAlt=" " 
            title="  " 
            description="   .        ."
            benefits={[
              "  ",
              "  ",
              "  "
            ]}
          />
        </section>

        <section className="cta-section">
          <h2 className="cta-title">  </h2>
          <p className="cta-text">
            StudyBuddy    !
          </p>
          <button 
            type="button" 
            className="cta-button"
          >
             
          </button>
        </section>
      </main>
    </div>
  );
};

export default App;